import React, { useState } from 'react';
import { Shield, Users, Smartphone, MapPin, BarChart3, Settings, Bell, LogOut, Search, Filter, Download, AlertTriangle, CheckCircle, XCircle, Eye, Mail, Phone, Building, X, MoreVertical } from 'lucide-react';

export default function AdminEmployeesPage() {
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedDepartment, setSelectedDepartment] = useState('all');
  const [selectedStatus, setSelectedStatus] = useState('all');
  const [showFilters, setShowFilters] = useState(false);
  const [selectedEmployees, setSelectedEmployees] = useState([]);
  const [showEmployeeDetails, setShowEmployeeDetails] = useState(false);
  const [selectedEmployee, setSelectedEmployee] = useState(null);

  const employees = [
    { id: 1, name: 'John Doe', email: 'john.doe@company.com', phone: '+1 234 567 8900', department: 'Engineering', position: 'Senior Developer', devices: 3, compliant: 2, status: 'active', location: 'Nagpur, India', joinDate: 'Jan 15, 2023', lastActive: '2 hours ago', avatar: 'JD' },
    { id: 2, name: 'Jane Smith', email: 'jane.smith@company.com', phone: '+1 234 567 8901', department: 'Marketing', position: 'Marketing Manager', devices: 2, compliant: 2, status: 'active', location: 'Mumbai, India', joinDate: 'Mar 22, 2023', lastActive: '5 minutes ago', avatar: 'JS' },
    { id: 3, name: 'Mike Johnson', email: 'mike.j@company.com', phone: '+1 234 567 8902', department: 'Sales', position: 'Sales Executive', devices: 2, compliant: 1, status: 'inactive', location: 'Delhi, India', joinDate: 'Jul 10, 2022', lastActive: '1 day ago', avatar: 'MJ' },
    { id: 4, name: 'Sarah Williams', email: 'sarah.w@company.com', phone: '+1 234 567 8903', department: 'HR', position: 'HR Manager', devices: 1, compliant: 1, status: 'active', location: 'Pune, India', joinDate: 'Feb 5, 2023', lastActive: '30 minutes ago', avatar: 'SW' },
    { id: 5, name: 'David Brown', email: 'david.b@company.com', phone: '+1 234 567 8904', department: 'Engineering', position: 'Lead Engineer', devices: 4, compliant: 3, status: 'active', location: 'Bangalore, India', joinDate: 'Sep 18, 2022', lastActive: '1 hour ago', avatar: 'DB' },
    { id: 6, name: 'Emily Davis', email: 'emily.d@company.com', phone: '+1 234 567 8905', department: 'Operations', position: 'Operations Lead', devices: 2, compliant: 2, status: 'active', location: 'Hyderabad, India', joinDate: 'May 12, 2023', lastActive: '15 minutes ago', avatar: 'ED' },
    { id: 7, name: 'Robert Wilson', email: 'robert.w@company.com', phone: '+1 234 567 8906', department: 'Sales', position: 'Senior Sales Rep', devices: 3, compliant: 2, status: 'active', location: 'Chennai, India', joinDate: 'Nov 3, 2022', lastActive: '3 hours ago', avatar: 'RW' },
    { id: 8, name: 'Lisa Anderson', email: 'lisa.a@company.com', phone: '+1 234 567 8907', department: 'Marketing', position: 'Content Strategist', devices: 2, compliant: 1, status: 'inactive', location: 'Kolkata, India', joinDate: 'Aug 20, 2023', lastActive: '2 days ago', avatar: 'LA' }
  ];

  const departments = ['all', 'Engineering', 'Marketing', 'Sales', 'HR', 'Operations'];

  const filteredEmployees = employees.filter(emp => {
    const matchesSearch = emp.name.toLowerCase().includes(searchQuery.toLowerCase()) || emp.email.toLowerCase().includes(searchQuery.toLowerCase()) || emp.department.toLowerCase().includes(searchQuery.toLowerCase());
    const matchesDepartment = selectedDepartment === 'all' || emp.department === selectedDepartment;
    const matchesStatus = selectedStatus === 'all' || emp.status === selectedStatus;
    return matchesSearch && matchesDepartment && matchesStatus;
  });

  const handleSelectEmployee = (id) => {
    setSelectedEmployees(prev => prev.includes(id) ? prev.filter(empId => empId !== id) : [...prev, id]);
  };

  const handleSelectAll = () => {
    if (selectedEmployees.length === filteredEmployees.length) {
      setSelectedEmployees([]);
    } else {
      setSelectedEmployees(filteredEmployees.map(emp => emp.id));
    }
  };

  const handleViewDetails = (employee) => {
    setSelectedEmployee(employee);
    setShowEmployeeDetails(true);
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Admin Navbar */}
      <nav className="bg-white border-b border-gray-200 sticky top-0 z-50 shadow-sm">
        <div className="px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-gradient-to-br from-blue-600 to-purple-600 rounded-lg shadow-lg">
                <Shield className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-xl font-bold text-gray-900">BYOD Admin Portal</h1>
                <p className="text-xs text-gray-500">Employee Management</p>
              </div>
            </div>
            <div className="flex items-center gap-4">
              <button className="relative p-2 text-gray-600 hover:bg-gray-100 rounded-lg">
                <Bell className="w-6 h-6" />
                <span className="absolute top-1 right-1 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center font-semibold">5</span>
              </button>
              <div className="flex items-center gap-3 pl-4 border-l border-gray-200">
                <div className="text-right">
                  <p className="text-sm font-semibold text-gray-900">Admin User</p>
                  <p className="text-xs text-gray-500">System Administrator</p>
                </div>
                <div className="w-10 h-10 bg-gradient-to-br from-yellow-500 to-orange-600 rounded-full flex items-center justify-center text-white font-semibold">AD</div>
                <button className="p-2 text-gray-600 hover:bg-red-50 hover:text-red-600 rounded-lg">
                  <LogOut className="w-5 h-5" />
                </button>
              </div>
            </div>
          </div>
        </div>
      </nav>

      <div className="flex">
        {/* Sidebar */}
        <aside className="w-64 bg-white border-r border-gray-200 min-h-screen sticky top-16">
          <nav className="p-4 space-y-2">
            <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium">
              <BarChart3 className="w-5 h-5" />Dashboard
            </button>
            <button className="w-full flex items-center gap-3 px-4 py-3 bg-blue-50 text-blue-600 rounded-lg font-medium">
              <Users className="w-5 h-5" />Employees
            </button>
            <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium">
              <Smartphone className="w-5 h-5" />All Devices
            </button>
            <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium">
              <MapPin className="w-5 h-5" />Geolocation
              <span className="ml-auto px-2 py-0.5 bg-green-100 text-green-700 text-xs rounded-full font-semibold">LIVE</span>
            </button>
            <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium">
              <Shield className="w-5 h-5" />Compliance
            </button>
            <div className="pt-4 mt-4 border-t border-gray-200">
              <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium">
                <Settings className="w-5 h-5" />Settings
              </button>
            </div>
          </nav>
        </aside>

        {/* Main Content */}
        <main className="flex-1 p-6 space-y-6">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-3xl font-bold text-gray-900">Employee Management</h2>
              <p className="text-gray-600 mt-1">Manage and monitor all employees in the BYOD system</p>
            </div>
            <div className="flex gap-3">
              <button className="flex items-center gap-2 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50">
                <Download className="w-4 h-4" />Export
              </button>
              <button className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">
                <Smartphone className="w-4 h-4" />Add Employee
              </button>
            </div>
          </div>

          {/* Stats */}
          <div className="grid grid-cols-4 gap-4">
            <div className="bg-white rounded-lg shadow-sm border p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600">Total Employees</p>
                  <p className="text-2xl font-bold text-gray-900 mt-1">{employees.length}</p>
                </div>
                <Users className="w-8 h-8 text-blue-600" />
              </div>
            </div>
            <div className="bg-white rounded-lg shadow-sm border p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600">Active</p>
                  <p className="text-2xl font-bold text-green-600 mt-1">{employees.filter(e => e.status === 'active').length}</p>
                </div>
                <CheckCircle className="w-8 h-8 text-green-600" />
              </div>
            </div>
            <div className="bg-white rounded-lg shadow-sm border p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600">Inactive</p>
                  <p className="text-2xl font-bold text-orange-600 mt-1">{employees.filter(e => e.status === 'inactive').length}</p>
                </div>
                <XCircle className="w-8 h-8 text-orange-600" />
              </div>
            </div>
            <div className="bg-white rounded-lg shadow-sm border p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600">Non-Compliant</p>
                  <p className="text-2xl font-bold text-red-600 mt-1">{employees.filter(e => e.devices > e.compliant).length}</p>
                </div>
                <AlertTriangle className="w-8 h-8 text-red-600" />
              </div>
            </div>
          </div>

          {/* Search and Filters */}
          <div className="bg-white rounded-lg shadow-sm border p-4">
            <div className="flex gap-4">
              <div className="flex-1 relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                <input type="text" value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)} placeholder="Search by name, email, or department..." className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" />
              </div>
              <button onClick={() => setShowFilters(!showFilters)} className="flex items-center gap-2 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50">
                <Filter className="w-4 h-4" />Filters
              </button>
            </div>
            {showFilters && (
              <div className="mt-4 pt-4 border-t flex gap-4">
                <div className="flex-1">
                  <label className="block text-sm font-medium text-gray-700 mb-2">Department</label>
                  <select value={selectedDepartment} onChange={(e) => setSelectedDepartment(e.target.value)} className="w-full px-3 py-2 border rounded-lg">
                    {departments.map(dept => <option key={dept} value={dept}>{dept === 'all' ? 'All Departments' : dept}</option>)}
                  </select>
                </div>
                <div className="flex-1">
                  <label className="block text-sm font-medium text-gray-700 mb-2">Status</label>
                  <select value={selectedStatus} onChange={(e) => setSelectedStatus(e.target.value)} className="w-full px-3 py-2 border rounded-lg">
                    <option value="all">All Status</option>
                    <option value="active">Active</option>
                    <option value="inactive">Inactive</option>
                  </select>
                </div>
              </div>
            )}
          </div>

          {/* Bulk Actions */}
          {selectedEmployees.length > 0 && (
            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
              <div className="flex items-center justify-between">
                <p className="text-sm font-medium text-blue-900">{selectedEmployees.length} employee(s) selected</p>
                <div className="flex gap-2">
                  <button className="px-4 py-2 bg-white border border-blue-300 text-blue-700 rounded-lg hover:bg-blue-50 text-sm">Export Selected</button>
                  <button onClick={() => setSelectedEmployees([])} className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm">Clear</button>
                </div>
              </div>
            </div>
          )}

          {/* Table */}
          <div className="bg-white rounded-lg shadow-sm border overflow-hidden">
            <table className="w-full">
              <thead className="bg-gray-50 border-b">
                <tr>
                  <th className="px-6 py-3 text-left">
                    <input type="checkbox" checked={selectedEmployees.length === filteredEmployees.length && filteredEmployees.length > 0} onChange={handleSelectAll} className="rounded" />
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Employee</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Contact</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Department</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Devices</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Status</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Last Active</th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y">
                {filteredEmployees.map((emp) => (
                  <tr key={emp.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4">
                      <input type="checkbox" checked={selectedEmployees.includes(emp.id)} onChange={() => handleSelectEmployee(emp.id)} className="rounded" />
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-3">
                        <div className="w-10 h-10 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white font-semibold">{emp.avatar}</div>
                        <div>
                          <p className="font-semibold text-gray-900">{emp.name}</p>
                          <p className="text-sm text-gray-500">{emp.position}</p>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-sm">
                        <p className="text-gray-900 flex items-center gap-1"><Mail className="w-3 h-3 text-gray-400" />{emp.email}</p>
                        <p className="text-gray-500 flex items-center gap-1 mt-1"><Phone className="w-3 h-3 text-gray-400" />{emp.phone}</p>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <span className="inline-flex items-center gap-1 px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm">
                        <Building className="w-3 h-3" />{emp.department}
                      </span>
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-2">
                        <span className="text-sm font-medium">{emp.devices}</span>
                        {emp.devices !== emp.compliant && <span className="px-2 py-0.5 bg-red-100 text-red-700 text-xs rounded-full">{emp.devices - emp.compliant} issues</span>}
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      {emp.status === 'active' ? (
                        <span className="inline-flex items-center gap-1 px-3 py-1 bg-green-100 text-green-700 rounded-full text-sm">
                          <div className="w-2 h-2 bg-green-500 rounded-full"></div>Active
                        </span>
                      ) : (
                        <span className="inline-flex items-center gap-1 px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm">
                          <div className="w-2 h-2 bg-gray-500 rounded-full"></div>Inactive
                        </span>
                      )}
                    </td>
                    <td className="px-6 py-4"><span className="text-sm text-gray-600">{emp.lastActive}</span></td>
                    <td className="px-6 py-4">
                      <div className="flex gap-2">
                        <button onClick={() => handleViewDetails(emp)} className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg" title="View Details">
                          <Eye className="w-4 h-4" />
                        </button>
                        <button className="p-2 text-gray-600 hover:bg-gray-100 rounded-lg">
                          <MoreVertical className="w-4 h-4" />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
            <div className="border-t px-6 py-4 flex items-center justify-between">
              <p className="text-sm text-gray-600">Showing {filteredEmployees.length} of {employees.length} employees</p>
              <div className="flex gap-2">
                <button className="px-4 py-2 border rounded-lg hover:bg-gray-50 text-sm">Previous</button>
                <button className="px-4 py-2 bg-blue-600 text-white rounded-lg text-sm">1</button>
                <button className="px-4 py-2 border rounded-lg hover:bg-gray-50 text-sm">Next</button>
              </div>
            </div>
          </div>
        </main>
      </div>

      {/* Employee Details Modal */}
      {showEmployeeDetails && selectedEmployee && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="sticky top-0 bg-white border-b px-6 py-4 flex items-center justify-between">
              <h3 className="text-2xl font-bold text-gray-900">Employee Details</h3>
              <button onClick={() => setShowEmployeeDetails(false)} className="p-2 text-gray-400 hover:text-gray-600 rounded-lg">
                <X className="w-6 h-6" />
              </button>
            </div>
            <div className="p-6 space-y-6">
              <div className="flex items-start gap-6">
                <div className="w-20 h-20 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white font-bold text-2xl">{selectedEmployee.avatar}</div>
                <div className="flex-1">
                  <h4 className="text-2xl font-bold text-gray-900">{selectedEmployee.name}</h4>
                  <p className="text-gray-600 mt-1">{selectedEmployee.position}</p>
                  <div className="flex gap-4 mt-3">
                    {selectedEmployee.status === 'active' ? (
                      <span className="px-3 py-1 bg-green-100 text-green-700 rounded-full text-sm font-medium">Active</span>
                    ) : (
                      <span className="px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm">Inactive</span>
                    )}
                    <span className="text-sm text-gray-500">ID: EMP-{selectedEmployee.id.toString().padStart(4, '0')}</span>
                  </div>
                </div>
              </div>
              <div className="grid grid-cols-2 gap-6">
                <div>
                  <p className="text-sm text-gray-500 mb-1">Email</p>
                  <p className="font-medium text-gray-900">{selectedEmployee.email}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500 mb-1">Phone</p>
                  <p className="font-medium text-gray-900">{selectedEmployee.phone}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500 mb-1">Department</p>
                  <p className="font-medium text-gray-900">{selectedEmployee.department}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500 mb-1">Location</p>
                  <p className="font-medium text-gray-900">{selectedEmployee.location}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500 mb-1">Join Date</p>
                  <p className="font-medium text-gray-900">{selectedEmployee.joinDate}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500 mb-1">Last Active</p>
                  <p className="font-medium text-gray-900">{selectedEmployee.lastActive}</p>
                </div>
              </div>
              <div className="bg-gray-50 rounded-lg p-4">
                <h5 className="font-semibold text-gray-900 mb-3">Device Summary</h5>
                <div className="grid grid-cols-3 gap-4 text-center">
                  <div>
                    <p className="text-2xl font-bold text-gray-900">{selectedEmployee.devices}</p>
                    <p className="text-sm text-gray-600">Total Devices</p>
                  </div>
                  <div>
                    <p className="text-2xl font-bold text-green-600">{selectedEmployee.compliant}</p>
                    <p className="text-sm text-gray-600">Compliant</p>
                  </div>
                  <div>
                    <p className="text-2xl font-bold text-red-600">{selectedEmployee.devices - selectedEmployee.compliant}</p>
                    <p className="text-sm text-gray-600">Issues</p>
                  </div>
                </div>
              </div>
              <div className="flex gap-3">
                <button className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">View Devices</button>
                <button className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50">Edit Profile</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}